import React from 'react'
import Roulette from '../Roulette'

const Section2 = () => {

  const language = 'es'

  const title = {
    es: 'Pide a Domicilio',
    cat: '',
    it: 'Ordina a Domiclio'
  }
  const description = {
    es: 'Pide a casa y recibe promociones adicionales a tu domicilio!',
    cat: '',
    it: 'Ordina e ricevi le nostre spcialita a casa tua!'
  }
  const button = {
    es: 'Pide ahora!',
    cat: '',
    it: ''
  }


  return (
    <div className='section2'>
      <div className='container'>
        <div className='left'>
          <div className='name'>Asia Delivery</div>
          <div className='title'>{title[language]}</div>
          <div className='description font4'>{description[language]}</div>
          <a href='./pdfs/CartaLlevar.pdf' target='blank' >
            <div className='button'>{button[language]}</div>
          </a>
        </div>
        <div className='right' >
          <Roulette />
        </div>
      </div>
    </div>
  )
}

export default Section2